:root {
  --font-outfit: "Outfit", sans-serif;
}

:root {
  --soft-blue: hsl(215, 51%, 70%);
  --cyan: hsl(178, 100%, 50%);
  --vdk-bl-mbg: hsl(217, 54%, 11%);
  --vdk-bl-cbg: hsl(216, 50%, 16%);
  --Vdk-bl-lin: hsl(215, 32%, 27%);
  --white: hsl(0, 0%, 100%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-outfit);
  background: var(--vdk-bl-mbg);
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

img {
  max-width: 100%;
}

h1,
h2,
h3 {
  margin: 0;
  font-weight: normal;
  line-height: 1.1;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

p {
  margin: 0;
}

.container {
  max-width: 60rem;
  margin: 0 auto;
}
@media (min-width: 61.875em) {
  .container {
    transform: translate(0, 0);
  }
}

.card {
  background: var(--vdk-bl-cbg);
  padding: 1.5625rem;
  margin: 3.875rem auto;
  max-width: 20.625rem;
  border-radius: 0.9375rem;
}
.card__img {
  margin-bottom: 1.875rem;
  height: 17.375rem;
  width: 17.375rem;
  position: relative;
}
.card__img--hover {
  display: grid;
  position: absolute;
  top: 0;
  left: 0;
  height: 17.375rem;
  width: 100%;
  border-radius: 0.375rem;
  transition: 0.4s ease-in-out;
  opacity: 0;
}
.card__img--hover > img {
  place-self: center;
}
.card__img--hover:hover {
  background: rgba(0, 167, 192, 0.7);
  opacity: 1;
}
.card__img img {
  border-radius: 0.375rem;
}
.card > a {
  display: block;
  font-size: 1.375rem;
  font-weight: 600;
  color: var(--white);
  transition: 0.3s ease-in-out;
  margin-bottom: 1.25rem;
}
.card > a:hover {
  color: var(--cyan);
}
.card > p {
  font-size: 1.0625rem;
  max-width: 17.1875rem;
  line-height: 1.375rem;
  color: var(--soft-blue);
  margin-bottom: 1.375rem;
  font-weight: 300;
}
.card ul {
  display: flex;
  justify-content: space-between;
  font-size: 1rem;
  color: var(--soft-blue);
  margin-bottom: 1.5625rem;
}
.card ul li {
  display: flex;
  align-items: center;
  font-weight: 400;
}
.card ul li:first-child {
  color: var(--cyan);
  font-weight: 700;
}
.card ul li img {
  margin-right: 0.4375rem;
}
.card__atrib {
  display: flex;
  align-items: center;
  color: var(--soft-blue);
  position: relative;
  padding-top: 1rem;
  font-weight: 300;
}
.card__atrib > p {
  margin-left: 0.9375rem;
}
.card__atrib a {
  font-weight: 400;
  color: var(--soft-blue);
  transition: 0.3s ease-in-out;
}
.card__atrib a:hover {
  color: var(--cyan);
}
.card__atrib img {
  width: 2.0625rem;
  height: 2.0625rem;
  border-radius: 50%;
  border: 0.0625rem solid var(--white);
}
.card__atrib::before {
  content: "";
  width: 100%;
  height: 0.0625rem;
  background: var(--Vdk-bl-lin);
  position: absolute;
  top: 0;
  left: 0;
}
@media (min-width: 48.125em) {
  .card {
    margin-top: 8.5625rem;
    margin-bottom: 8.5625rem;
    box-shadow: 0 0 18px 10px rgba(0, 0, 0, 0.1);
  }
}